function debounce(callback, delay = 60) {
    ifNotFunction(callback);
    let timer;
    return function (...args) {
        if (timer) {
            clearTimeout(timer);
        }
        timer = setTimeout(() => {
            callback.call(this, ...args);
        }, delay);
    };
}

/**
 * 节流
 * @param fn {*}  执行函数
 * @param delay {*}  节流时间,毫秒
 * @returns {Function}
 */
function throttle(callback, delay = 60) {
    ifNotFunction(callback);
    let flag = true;
    return function (...args) {
        if (flag) {
            flag = false;
            const res = callback.call(this, ...args);
            setTimeout(() => {
                flag = true;
            }, delay);
            return res;
        }
        return undefined;
    };
}

